@import url("https://fonts.googleapis.com/css2?family=Monoton&family=Quicksand:wght@500;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%; /* 1rem = 10px, easier for rem calculation */
}

body {
  font-size: 2.4rem; /* Default font-size (24px) */
  font-family: "Quicksand", sans-serif;
  font-weight: 500;
  color: #5a3e2b;
}

.app {
  width: 100%;
  min-height: 100vh; /* Ensure the app takes at least the full viewport height */
  display: grid;
  grid-template-rows: auto auto 1fr auto;
}

/* Headline (H1) Styles */
h1 {
  text-align: center;
  background-color: #f4a226;
  font-family: "Monoton", sans-serif;
  font-size: 8rem;
  text-transform: uppercase;
  font-weight: 400;
  word-spacing: 30px;
  letter-spacing: -5px;
  padding: 2.4rem 0;
}

/* Add-form Section */
.add-form {
  background-color: #e5771f;
  padding: 2.8rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
}

h3 {
  margin-right: 1.6rem;
  font-size: 2.4rem;
}

button,
select,
input {
  background-color: #ffebb3;
  color: #5a3e2b;
  font-family: inherit;
  border: none;
  border-radius: 10rem;
  padding: 1.2rem 3.2rem;
  font-weight: 700;
  font-size: 1.8rem;
  cursor: pointer;
}

.add-form button {
  text-transform: uppercase;
  background-color: #76c7ad;
}

.form-row {
  display: flex;
  gap: 5px;
}

/* List Section */
.list {
  background-color: #5a3e2b;
  color: #ffebb3;
  padding: 4rem 0;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 3.2rem;
  align-items: center;
}

.actions button,
.list select {
  text-transform: uppercase;
  padding: 0.8rem 2.4rem;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0 0.8rem;
  background-color: #ffebb3;
  color: #5a3e2b;
  border: none;
  border-radius: 10rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.actions button:hover,
.list select:hover {
  background-color: #e5771f; /* Change color on hover */
  color: #fff;
}

.list select {
  appearance: none; /* Remove default arrow */
  -webkit-appearance: none; /* For Safari */
  -moz-appearance: none; /* For Firefox */
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%235a3e2b%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat;
  background-position: right 1rem center;
  background-size: 12px;
}

.list ul {
  list-style: none;
  width: 80%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.2rem;
  justify-content: center;
  align-content: start;
}

.list li {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}

input[type="checkbox"] {
  height: 2rem;
  width: 2rem;
  accent-color: #e5771f;
}

.list li button {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.8rem;
  padding: 0.8rem;
  transform: translateY(2px);
}

/* Stats Section */
.stats {
  background-color: #76c7ad;
  text-align: center;
  font-weight: 700;
  padding: 3.2rem 0;
}

/* Responsive Design */

/* For tablets and medium screens (768px and below) */
@media (max-width: 768px) {
  body {
    font-size: 2rem; /* Smaller base font for tablets */
  }

  h1 {
    font-size: 6rem;
    padding: 2rem 0;
  }

  .add-form {
    flex-direction: column; /* Stack inputs vertically */
    padding: 2rem 0;
    gap: 1rem; /* Add gap for stacked items */
  }

  .list ul {
    width: 90%;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }

  .stats {
    padding: 2.4rem 0;
  }

  .actions button,
  .list select {
    padding: 0.8rem 1.6rem;
    font-size: 1.2rem;
    margin: 0 0.5rem;
  }
}

/* For small tablets and large phones (600px and below) */
@media (max-width: 600px) {
  h1 {
    font-size: 5rem;
    word-spacing: 20px;
    letter-spacing: -3px;
  }

  .add-form {
    padding: 1.8rem 0;
  }

  h3 {
    font-size: 2.2rem;
  }

  button,
  select,
  input {
    padding: 1rem 2.4rem;
    font-size: 1.6rem;
  }

  .list ul {
    width: 95%;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  }

  .actions button,
  .list select {
    padding: 0.6rem 1.2rem;
    font-size: 1rem;
    margin: 0 0.4rem;
  }

  .list select {
    background-position: right 0.8rem center;
    background-size: 10px;
  }
}

/* For mobile phones (480px and below) */
@media (max-width: 480px) {
  html {
    font-size: 55%; /* Scale down font sizing for small screens */
  }

  body {
    font-size: 1.8rem;
  }

  h1 {
    font-size: 4rem;
    word-spacing: 15px;
    letter-spacing: -2px;
    padding: 1.6rem 0;
  }

  .add-form {
    gap: 0.5rem;
    padding: 1.6rem 0;
  }

  h3 {
    font-size: 2rem;
  }

  button,
  select,
  input {
    padding: 1rem 2rem;
    font-size: 1.4rem;
  }

  .list ul {
    width: 100%;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
  }

  .stats {
    padding: 2rem 0;
  }

  .actions button,
  .list select {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
    margin: 0 0.3rem;
  }

  .list select {
    background-position: right 0.6rem center;
    background-size: 8px;
  }
}

/* For very small screens (320px and below) */
@media (max-width: 320px) {
  h1 {
    font-size: 3.5rem;
    word-spacing: 10px;
    letter-spacing: -1px;
  }

  .add-form {
    padding: 1.2rem 0;
  }

  h3 {
    font-size: 1.8rem;
  }

  button,
  select,
  input {
    padding: 0.8rem 1.6rem;
    font-size: 1.2rem;
  }

  .list ul {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }

  .stats {
    padding: 1.6rem 0;
  }

  .actions button,
  .list select {
    padding: 0.4rem 0.8rem;
    font-size: 0.8rem;
    margin: 0 0.2rem;
  }

  .list select {
    background-position: right 0.5rem center;
    background-size: 6px;
  }
}
